@import 'src/assets/scss/colors';

.main_room {
  .course_list {
    width: 100%;
    .course_card_box {
      display: inline-block;
      width: 50%;
      padding: 10px;

      .course_card {
        padding: 20px 10px;
        display: block;
        box-shadow: 0 0 2px rgba(0,0,0,0.3);

        .title {
          width: 100%;
          em {
            font-size: 14px;
            color: $color-darkDarkGray;
            font-weight: bold;
          }
          span {
            position: absolute;
            float: right;

            button {
              margin: 0 2px;
            }
          }
        }
      }
    }
  }
}

.add_course {
  & > .content {
    & > em {
      font-weight: bold;
      font-size: 14px;
      color: $color-font-darkDarkGray;
    }
  }
}

.modal_design {
  position: relative;
  width: 500px;

  table {
    width: 100%;

    th {
      padding: 10px 5px 5px 5px;
      text-align: left;

      em {
        font-size: 12px;
        color: $color-font-darkDarkGray;
      }
    }
    td {
      padding: 5px;

      > .tag {
        svg > path {
          fill: $color-darkGray !important;
        }
      }

      & > input {
        width: 100%;
      }
      & > textarea {
        width: 100%;
        height: 200px;
      }

      & > button {
        width: 150px;
        height: 40px;

        em {
          font-weight: bold;
          font-size: 14px;
        }
      }
    }
  }
}